import { error, notice } from '@pnotify/core';
import '@pnotify/core/dist/BrightTheme.css';
import '@pnotify/core/dist/PNotify.css';

const callError = () => {
    const myError = error({
        title: ' !',
        sticker: false,
    });
}

const callNotice = () => {
    const myNotice = notice({
        title: '      !',
        sticker: false,
    });
}
export {callError, callNotice}